<template>
  <transition name="fade" mode="in-out">
    <section class='commentCon'>
      <v-Header>
        <span slot='text'>评论</span>
      </v-Header>
      <textarea placeholder='说点什么好呢...' class='commetText' v-model='commetText'></textarea>
      <div class='submitBtn'>提交评论</div>
    </section>
  </transition>
</template>
<script>
import Header from '../../components/header';
  export default {
    name: '',
    components: {'v-Header':Header},
    props: {},
    data () {
      return {
        commetText:'',
      }
    },
    watch: {},
    computed: {},
    created () {
      this.init()
    },
    methods: {
      init () {},
      initData () {}
    }
  }
</script>

<style lang="scss" scoped>
*{
      border: none;
      margin: 0;
      padding: 0;
    }
  textarea { 
    resize : none;
    &::-webkit-input-placeholder {
      color:rgb(173,173,173);
      text-indent: 1em;
    }
  }
.commentCon{
  position:fixed;
  top:0;
  left:0;
  right:0;
  bottom:0;
  background:rgb(242,242,242);
  .commetText{
    box-sizing: border-box;
    margin:0.1rem 0 0.25rem 0;
    height:3.46rem;
    width:100%;
    text-indent: 1em;
    font-size:0.3rem;
    padding:0.35rem 0 0.35rem 0;
  }
  .submitBtn{
    width:6.9rem;
    height:0.88rem;
    margin:0 auto;
    background:rgb(57,166,206);
    border-radius: 0.15rem;
    font-size:0.36rem;
    line-height:0.88rem;
    color:white;
    text-align:center;
  }
}
</style>



